<template>
    <div class="myCards">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">套餐管理</span>
            <span class="back"></span>
            <span class="release" @click="release">添加
              <ul class="cardMenu" v-if="isSelecting" v-click-outside="hideMenu">
                <li @click="$router.push('/home/mine/myYearCard')">年卡</li>
                <li @click="$router.push('/home/mine/myTimeCard')">次卡</li>
                <li @click="$router.push('/home/mine/myPersonalCard')">私教</li>
              </ul>
            </span>
        </div>
        <div class="content">
            <div class="notice" v-show="cards.length === 0">您还没有设置任何套餐，快去添加吧！</div>
            <div class="cards">
                <div class="card" v-for="(card, index) in cards" :key="index" @click="manage(index)" :style="{ backgroundImage: card.type === '年卡' ? 'url(' + yearCardImg + ')' : (card.type === '次卡' ? 'url(' + timeCardImg + ')' : 'url(' + personalCardImg + ')') }">
                    <span class="discountPrice">
                        折扣价：<span class="orange">{{ card.realPrice }}</span>
                    </span>
                    <span class="orignalPrice">
                        <span style="text-decoration: line-through">
                            原&emsp;价：<span>{{ card.origPrice }}</span>
                        </span>
                        <span class="orange">
                            <span class="discount">{{ card.discount }}</span>
                        </span>
                    </span>
                    <div class="cardTip">{{ card.type }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ClickOutside from 'vue-click-outside'
import axios from 'axios'
export default {
  name: 'myCards',
  data () {
    return {
      yearCardImg: require('../../assets/img/yearCard.png'),
      timeCardImg: require('../../assets/img/timeCard.png'),
      personalCardImg: require('../../assets/img/personalCard.png'),
      isSelecting: false,
      cards: [],
      cardType1: 'yearCard',
      cardType2: 'timeCard',
      cardType3: 'personalCard'
    }
  },
  directives: {
    ClickOutside
  },
  mounted () {
    this.getCardList()
    console.log(this.cards)
    this.popupItem = this.$el
  },
  /* computed: {
    backgroundImg: function (index) {
      if (this.cards[index].type === '年卡') {
        return this.yearCardImg
      } else if (this.cards[index].type === '次卡') {
        return this.timeCardImg
      } else {
        return this.personalCardImg
      }
    }
  }, */
  methods: {
    hideMenu () {
      this.isSelecting = false
    },
    back () {
      this.$router.go(-1)
    },
    release () {
      this.isSelecting = !this.isSelecting
    },
    manage (index) {
      if (this.cards[index].type === '年卡') {
        this.$router.push('/home/mine/myYearCard')
      } else if (this.cards[index].type === '次卡') {
        this.$router.push('/home/mine/myTimeCard')
      } else {
        this.$router.push('/home/mine/myPersonalCard')
      }
    },
    getCardList () {
      axios.get('http://47.96.76.137:8088/bundleManageMent/bundleList')
        .then(res => {
          this.cards = res.data.data
        })
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .release {
    position: absolute;
    right: 0;
  }

  img {
    width: 100%;
  }

  .content {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .card {
    width: 100%;
    border-radius: 0.2rem;
    padding: 1.5rem 0;
    position: relative;
    overflow: hidden;
    margin-top: 0.42667rem;
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
  }

  .discountPrice,
  .orignalPrice {
    display: block;
    text-align: start;
    padding: 0.1rem 1.5rem;
    margin: 0 auto;
  }

  .orange {
    color: #ffd200;
  }

  .cardTip {
    position: absolute;
    left: 0;
    top: 0;
    width: 1.2rem;
    height: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffd200;
    border-bottom-right-radius: 0.2rem;
  }

  .cardMenu {
    position: absolute;
    top: 1.1rem;
    list-style: none;
    border: 1px solid #f5f5f5;
    border-radius: 0.15rem;
    background: #fff;
    padding: 0.2rem;
    z-index: 999;
  }

  .cardMenu li {
    padding: 0.2rem 0.1rem;
    color: #ffd200;
    font-size: 0.42667rem;
  }

  .cardMenu li:nth-child(2n + 2) {
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
  }

  .notice {
    font-size: 0.42667rem;
    color: #666666;
    margin-top: 0.5rem;
  }
</style>



